/***** Project Cards *****/
.blog-card {
    width: 95%;
    max-width: 800px;
    padding: 30px;
    margin: 0 -30px;

    border-radius: 8px;
}

.blog-card.loading {
    font-size: 24px;
    text-align: center;
    margin: 50px auto;
    font-family: 'EB Garamond', serif;
    color: hsl(200, 10%, 30%);
}

/*** Header ***/
.blog-header {
    display: inline-block;

    text-decoration: none;
    
    /* To prevent click dead zone caused by tags */
    padding-bottom: 30px;
    margin-bottom: -30px;
    cursor: pointer;
}

.blog-title {
    font-family: Raleway, sans-serif;
    font-size: 30px;
    line-height: 1.2;
    color: hsl(200, 100%, 30%);
    font-weight: 500;
    text-overflow: ellipsis;

    margin: 0;
    margin-left: -3px;
}

.blog-subtitle {
    margin-top: 0;
    margin-bottom: 10px;
    font-family: Roboto, sans-serif;
    color: hsl(200, 10%, 55%);
    font-size: 12px;

    text-transform: uppercase;
    font-weight: 500;
    letter-spacing: 0.1em;
}

.blog-subtitle .date {
    margin-right: 8px;
}

.blog-subtitle .read-time {
    margin-left: 8px;
}

/*** Content ***/
a.blog-info {
    font-size: 18px;
    font-family: 'EB Garamond', serif;
    line-height: 1.5;
    text-decoration: none;
    color: hsl(200, 10%, 10%);

    display: block;
    margin-bottom: 20px;
    padding-top: 1px;
    position: relative;

    cursor: pointer;
}

.blog-info img {
    display: block;
    max-width: 100%;
    margin: 10px auto;
}

.blog-info a.reference {
    display: none;
}

.blog-info-cover {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(to bottom, hsla(200, 10%, 96%, 0), hsl(200, 10%, 96%, 0.98) 85%, hsl(200, 10%, 96%));
    z-index: 1;
}


/* Tags */
.blog-tags {
    display: flex;
    flex-direction: row;

    align-items: center;
    flex-wrap: wrap;

    margin-top: 5px;
    margin-bottom: 5px;
}

.blog-tag {
    background-color: hsl(200, 20%, 92%);
    border: 1px solid hsl(200, 10%, 90%);

    color: hsl(200, 20%, 40%);
    font-size: 12px;
    padding: 2px 7px;
    letter-spacing: 0.05em;
    margin-top: 6px;
    margin-right: 8px;

    white-space: nowrap;

    cursor: pointer;

    transition: background-color 0.15s ease-in-out;
}

.blog-tag:hover {
    background-color: hsl(200, 10%, 85%);
}


/***** Media Queries *****/
@media (max-width: 600px) {
    .blog-card {
        margin: 0;
        margin-top: 20px;
        margin-bottom: -20px;
        padding: 0;
    }

    .blog-card:hover {
        background-color: initial;
    }

    .blog-title {
        font-size: 20px;
        margin-left: -2px;
    }

    p.blog-subtitle {
        margin-bottom: 8px;
    }

    .blog-tags {
        display: none;
    }

    .blog-tag {
        padding: 2px 8px;
    }

    a.blog-info {
        padding-top: 0;
        font-size: 16px;
    }
}


@media print {
    .blog-card {
        page-break-inside: avoid;
    }

    .blog-title, .blog-subtitle, a.blog-info {
        color: black;
    }

    .blog-tag {
        background-color: white;
        border: 1px solid black;
        box-sizing: border-box;
        color: black;
    }

    .blog-info-cover {
        display: none;
    }
}
